<template>
  <div class="love">
    <div class="text">
      ÂìàÂìàÂìàÂìàÂìàÔºåÊâì‰∏çËøáÊàëÂêß-----Ê≤°ÊúâÂäûÊ≥ïÔºåÊàëÂ∞±ÊòØËøô‰πàÂº∫Â§ßÔºÅ
    </div>



  </div>
</template>

<script>
import '../assets/js/m.js'
//import $ from 'n-zepto'

export default {
  name: 'Love',
  data () {
    return {
      type: 0,
      contentArr: [
        'ÊàøËØÅÂÜô‰Ω†ÂêçüòÅ', '‰øùÂ§ßüòÅ', 'ÊàëÂ¶à‰ºöÊ∏∏Ê≥≥üòÅ', 'Â∑•ËµÑÂÖ®Áªô‰Ω†üòÅ', 'Â§©Â§©ÁªôÊåâÊë©üòÅ', 'ÂêµÊû∂ÂÖàÈÅìÊ≠âüòÅ', 'ÁªèÂ∏∏ÈÄÅÁ§ºÁâ©üòÅ', 'Â§©Â§©ÊÉÖ‰∫∫ËäÇüòÅ',
        'ËÆ©‰Ω†Ê¨∫Ë¥ü‰∏ÄËæàÂ≠êüòÅ', 'Â§©Â§©ÈÄó‰Ω†ÂºÄÂøÉüòÅ'
      ]
    }
  },
  created () {
    this.$messagebox.confirm('', {
      title: 'ÂÅöÊàëÂ•≥ÊúãÂèã',
      message: 'Â•ΩÂêóÔºüÂ•ΩÂêóÔºüÂ•ΩÂêóÔºü',
      showConfirmButton: true,
      confirmButtonText: 'Â•Ω',
      showCancelButton: true,
      cancelButtonText: '‰∏çË¶Å'
    }).then(action => {
      if (action == 'confirm') {     //Á°ÆËÆ§ÁöÑÂõûË∞É
        this.success()

      }

    }).catch(err => {
      if (err == 'cancel') {     //ÂèñÊ∂àÁöÑÂõûË∞É
        this.callback()

      }
    })


  },
  mounted () {

  },
  methods: {
    callback () {
      if (this.type >= this.contentArr.length) {
        this.type = 0
      }

      const content = this.contentArr[this.type]
      this.type++

      this.dialog(null, content, this.success, this.callback)
    },
    success () {
      this.$messagebox.alert('‰ªé‰ªäÂ§©Ëµ∑‰Ω†‰∏çÊòØÂä®Áâ©‰∫Ü', '‰Ω†Â§™ÊúâÁúºÂÖâ‰∫ÜÔºÅ')

    },

    dialog (title = '', content = '', success, fail) {
      this.$messagebox.confirm('', {
        title: title,
        message: content,
        showConfirmButton: true,
        confirmButtonText: 'Â•Ω',
        showCancelButton: true,
        cancelButtonText: '‰∏çË¶Å'
      }).then(action => {
        if (action == 'confirm') {     //Á°ÆËÆ§ÁöÑÂõûË∞É
          success && success()
        }

      }).catch(err => {
        if (err == 'cancel') {     //ÂèñÊ∂àÁöÑÂõûË∞É
          fail && fail()
        }
      })

    }

  }

}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import '../assets/css/pr2rem';

  .text {
    width: pr(600);
    height: pr(200);
    font-size: pr(30);
    position: fixed;
    top: 50%;
    left: 50%;
    margin-top: pr(-100);
    margin-left: pr(-300);
  }

</style>



